	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by Michael Lancaster","subtitle":"Software Engineer focused in Javascript applications","copyright":"© All rights reserved.","author":{"name":"Michael Lancaster","twitter":"weblancaster"},"url":"http://www.bymichaellancaster.com"}},"markdownRemark":{"id":"/Users/michaellancaster/Sites/github/michaellancaster-blog/src/pages/articles/2013-08-25-tonights-inspiration/index.md absPath of file >>> MarkdownRemark","html":"<p>I haven’t posted anything a while which is a shame but I had good reasons and I should be doing a post about it on my next post but this post is made to show what inspire me love this world what makes me think how wonderful things are and how much we can learn from our world.</p>\n<h2>Love and Life</h2>\n<p>Love. Perhaps just a word for some but at the same time very powerful. Love drives us to different things in life. Good things and bad things. Both..So many perspectives from different cultures and races but same origin. Your inside.</p>\n<p>Love inspire me in some many different ways and I’m not ashamed to say I believe in true love.\nAnd here it’s very interesting to see some ways people want to express love.</p>\n<p>You decide which one suits you.</p>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.2%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <div class=\"fluidMedia\">\n  <iframe src=\"//player.vimeo.com/video/22439234\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\" style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n</div>\n          </div>\n          </div>\n<br>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.2%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <div class=\"fluidMedia\">\n  <iframe src=\"//player.vimeo.com/video/35699279\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\" style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n</div>\n          </div>\n          </div>\n<br>\n<div class=\"fluidMedia\">\n  <iframe src=\"//player.vimeo.com/video/71495477?color=f7f9fa\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe>\n</div>\n<br>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.2%; position: relative; height: 0; overflow: hidden;margin-bottom: 1.0725rem\"\n          >\n            <div class=\"fluidMedia\">\n  <iframe src=\"//player.vimeo.com/video/61361236\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\" style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n</div>\n          </div>\n          </div>\n<h2>Technology and Inspiration</h2>\n<p>Another thing that inspires me can, changed and has being changed the world as love for good or for bad. Technology..but on this post I will show you what inspired me tonight..</p>\n<p>First one is how Netflix is delivering high quality WebKit based UIs to televisions, game consoles and Blu-ray players.\nthis talk will discuss fluid animation with hardware acceleration, achieving high framerates using accelerated compositing, responding to constant user input, as well as balancing strategies for best performance on over 450 high-end to low-end devices.</p>\n<div class=\"fluidMedia\">\n  <iframe src=\"//www.youtube.com/embed/xuMWhto62Eo\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe>\n</div>\n<br>\n<p>Second one discuss how programming has being changing people and the future of programming.\nThe way the speaker approach the subject is a very creative and interesting thing.</p>\n<div class=\"fluidMedia\">\n  <iframe src=\"//player.vimeo.com/video/71278954?title=0&amp;byline=0&amp;portrait=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe>\n</div>\n<br>\n<p>If it all doesn’t makes sense to you don’t worry lot of things in life doesn’t makes sense.</p>","fields":{"date":"2013-08-25"},"frontmatter":{"title":"Tonight's inspiration","description":"I haven't posted anything a while which is a shame but I had good reasons and I should be doing why on my next post but this post is made to show what inspire me in some nights, what makes me think how wonderful world is and how much we cna learn from it."}}},"pathContext":{"slug":"/articles/2013-08-25-tonights-inspiration/"}}

